import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";

import { ActivatedRoute } from '@angular/router';
import { CardElementsAdjustmentBean } from "app/pages/tzb/element-adjustment/card-status/card-status-exam/bean/card-status-exam.bean";
import * as _ from 'lodash';

@Component({
    selector: 'card-status-exam',
    templateUrl: './card-status-exam.component.html',
    styleUrls: ['./card-status-exam.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CardStatusExamComponent implements OnInit {

    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;
    handleDate: string;
    code: any;
    bean: CardElementsAdjustmentBean = new CardElementsAdjustmentBean();
    msgs: Message[];
    ch: any = CALENDAR_CN;
    guaranteeMode: any = [];
    noPromptDispaly: boolean = false;
    detailList: any = [];
    adjustmentTypeList: any = [];
    adjustmentModeList: any = [];
    consumeOverdrawQuotaType: any = [];
    adjustOrientation: any = [];
    funcTypeList: any = [];
    funcTypeAdd: any = [];
    statusAdjustTypeList: any = [];
    funcList: any;
    statusChangeCause: any = [];
    taskRate: string;
    canUp: boolean = false;
    flag: string;
    isHistory: boolean = false;
    cardStatusInfo: any;
    isShow: boolean = true;
    statusAdjustType: any;//
    func: string;//
    statusAdjustTypeAdd: any = [];
    beanExam: any;
    tablelist: any = [];

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        public routeInfo: ActivatedRoute,
    ) {
        //
        this.codeValues();
        this.statusAdjustTypeList = this.code['StatusAdjustType'];
        this.adjustmentTypeList = this.code['AdjustmentTypeList'];
        this.adjustmentModeList = this.code['AdjustmentUpdateType'];
        this.consumeOverdrawQuotaType = this.code['ConsumeOverdrawQuotaType'];
        this.adjustOrientation = this.code['AdjustOrientation'];
        this.funcTypeList = this.code['FuncTypeList'],
            this.guaranteeMode = this.code['CardGuaranteeMode'];
        this.statusChangeCause = this.code['StatusChangeCause'];
        this.statusAdjustType = this.code['StatusAdjustType'];
        this.statusAdjustTypeAdd = [];
        this.statusAdjustTypeAdd.push({ label: '', value: '' })
        // 
        this.detailList = this.routeInfo.snapshot.params;

        this.beanExam = JSON.parse(this.routeInfo.snapshot.params.dataMap)
        // this.bean = JSON.parse(this.routeInfo.snapshot.params.dataMap)

        this.taskRate = this.routeInfo.snapshot.params['taskRate'];
        this.flag = this.routeInfo.snapshot.params['markFlag'];
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
        if (this.taskRate == '1' && !this.isHistory) {
            this.canUp = true;
        } else {
            this.canUp = false;
        }

    }


    // 
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        this.bean.appSeqNo = this.beanExam.appSeqNo;
        this.bean.creditCardNo = this.beanExam.creditCardNo;

        let param1 = {
            appSeqNo: this.bean.appSeqNo,
            creditCardNo: this.bean.creditCardNo,
        }
        let ob = this.httpService.queryCeCardAdjustApp(param1);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tablelist = data.infoList == null ? [] : data.infoList;
                if(this.tablelist.length > 0){
                    let dataItem = this.tablelist[0];

                    for (let item in dataItem) {
                        this.bean[item] = dataItem[item];
                    }
                }
                this.queryCeCardAdjustApp();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

    // 
    queryCeCardAdjustApp() {
        let ss = this.beanExam.func.split('');
        this.funcList = []
        if (ss.length > 0) {
            ss.forEach(element => {
                if (element != '|') {
                    this.funcList.push(element);
                }
            });
        }
        if (!this.canUp) {
            this.isShow = false;
            this.funcTypeAdd = this.funcTypeList;
        }
        if (this.canUp) {
            this.queryCeCardByStatusInfo();
        }
    }

    // 
    queryCeCardByStatusInfo() {

        let param = {
            creditCardNo: this.bean.creditCardNo
        }
        this.httpService.queryCeCardByStatusInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // this.bean.timeLimit = data.timeLimit;
                // this.bean.prodNo = data.prodNo;
                // this.bean.prodName = data.prodName;
                // this.bean.businessType = data.businessType;
                this.statusAdjustType = data.statusAdjustTypeList,//
                    this.func = data.func;//
                let a = [];
                let funOption = [];
                if (this.statusAdjustType.length > 0) {
                    this.statusAdjustType.forEach(element => {
                        a.push(element[0])
                    });
                }
                for (let i = 0; i < this.statusAdjustTypeList.length; i++) {
                    let bool = true;
                    for (let j = 0; j < a.length; j++) {
                        if (a[j] == this.statusAdjustTypeList[i].value) {
                            bool = false
                            this.statusAdjustTypeAdd.push(this.statusAdjustTypeList[i])
                            break
                        }
                    }
                }
                this.statusAdjustTypeAdd = _.clone(this.statusAdjustTypeAdd)
                this.getStatusAdjustTypelIST();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

    // 
    getStatusAdjustTypelIST() {
        // 
        this.changeTheme(this.bean.statusAdjustType);
        if (this.bean.func && this.bean.func.length > 0) {
            let ss = this.bean.func.substring(0, this.bean.func.length - 1).split("|")
            for (let i = 0; i < this.funcTypeList.length; i++) {
                let bool = true;
                for (let j = 0; j < ss.length; j++) {
                    if (ss[j] == this.funcTypeList[i].value) {
                        this.funcList.push(ss[j])
                        bool = false
                        break
                    }
                }
            }
        }
    }

    // 
    changeValue() {
        this.funcTypeAdd = _.clone(this.funcTypeAdd);
        this.funcList = _.clone(this.funcList);
        this.cardStatusInfo = _.clone(this.bean)
    }

    // 
    changeTheme(param) {
        this.funcList = [];
        this.funcTypeAdd = [];
        if (param == '2' || param == '3') {
            this.isShow = false;
        } else {
            this.isShow = true;
        }
        let mm = ''
        if (this.statusAdjustType.length > 0) {
            this.statusAdjustType.forEach(element => {
                if (param == element[0]) {
                    mm = element;
                }
            });
        }
        if (mm.indexOf(',') == -1) {
            this.funcTypeAdd = [];
        } else {
            let ss = mm.split(',')[1].split('|');
            for (let i = 0; i < this.funcTypeList.length; i++) {
                let bool = true;
                for (let j = 0; j < ss.length; j++) {
                    if (ss[j] == this.funcTypeList[i].value) {
                        bool = false
                        this.funcTypeAdd.push(this.funcTypeList[i])
                        break
                    }
                }
            }
        }
        let arr = []
        if (this.funcTypeAdd.length >= 1) {
            this.funcTypeAdd.forEach(element => {
                if (element.value != '') {
                    arr.push(element)
                }
            });
            this.funcTypeAdd = [];
            this.funcTypeAdd = arr;
        }
        let arrResult = [];
        this.funcTypeAdd.forEach(element => {
            arrResult.push(element)
        });
        this.funcTypeAdd = _.clone(arrResult);
        this.funcList = _.clone(this.funcList);
        this.cardStatusInfo = _.clone(this.bean)
    }
}