/**
 * @author fanhualing 2018-9-21 
 */
import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { ConfirmationService, Message } from "primeng/primeng";
import { CusOperationHttpService } from 'app/pages/tzb/custom/http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CardStatusAdjustmentQueryDetailsBean } from 'app/pages/tzb/element-adjustment/card-status/card-status-adjustment-query-update/bean/card-status-adjustment-query-update.bean';
import * as _ from 'lodash';
import { AppproResultBean, SubApproveMapBean, BussinessMap } from 'app/pages/tzb/exam-appr-comp/custom/custom-group/bean/AppproResult.bean';
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";

@Component({
    selector: 'cardStatusAdjustmentUpdateDetails',
    templateUrl: './card-status-adjustment-query-update.component.html',
    styleUrls: ['./card-status-adjustment-query-update.component.scss'],
    providers: [Commfunc, ConfirmationService]
})
export class CardStatusAdjustmentUpdateDetailsComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    @Input('key-label') keyLabel: any;
    @Input('kind-do') kindDo: string = '';

    code: any;
    adjustmentTypeList: any = [];
    adjustmentModeList: any = [];
    consumeOverdrawQuotaType: any = [];
    adjustOrientation: any = [];
    bean: CardStatusAdjustmentQueryDetailsBean = new CardStatusAdjustmentQueryDetailsBean();
    msgs: Message[];
    statusAdjustTypeList: any = [];
    funcTypeList: any = [];
    // funcList: any;
    zhanghu: boolean = false;
    guaranteeMode: any = [];
    statusChangeCause: any = [];
    waitSelUserList: any;
    outVal: any;
    aaaa: boolean = false;

    // 
    creditCardNo: string;///
    custNo: string;//
    custName: string;//
    prodNo: string;//
    prodName: string;//
    businessType: string;//
    statusAdjustType: any;//
    func: string;//
    isShow: boolean = true;
    funcList: any;

    statusAdjustTypeAdd: any = [];
    funcTypeAdd: any = [];
    statusAdjustTypeBefor: string = '';
    noPromptDispaly: boolean = false;
    nameList: any;
    statusCardAdjustment: any = [];

    // ------------fanhl------------------
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    bussinessMap: BussinessMap = new BussinessMap();
    examineDisplay: any;
    extableData: any;
    ex: any = [];
    checkValueNode: string;  //
    checkValuePeople: string;
    checkValuePeopleSign: any = []; //
    mm: any = [];
    chosseNodeIndex: string;
    msgs1: Message[];
    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;
    taskId: string;

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        public customeHttpService: CustomeHttpService,
    ) {
        //
        this.codeValues();
        this.guaranteeMode = this.code['CardGuaranteeMode'];
        this.statusChangeCause = this.code['StatusChangeCause'];
        this.funcTypeList = this.code['FuncTypeList']
        this.statusAdjustTypeList = this.code['StatusAdjustType'];
        this.adjustmentTypeList = this.code['AdjustmentTypeList'];
        this.adjustmentModeList = this.code['AdjustmentUpdateType'];
        this.consumeOverdrawQuotaType = this.code['ConsumeOverdrawQuotaType'];
        this.adjustOrientation = this.code['AdjustOrientation'];
        this.statusAdjustType = this.code['StatusAdjustType'];
        this.statusAdjustTypeAdd = [];
        this.statusAdjustTypeAdd.push({ label: '', value: '' })
        this.statusCardAdjustment = this.code['statusCardAdjustment'];

        this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));

        // 
        if (this.user1.userId) {
            this.userId = this.user1.userId;
        }
        if (this.user1.orgId) {
            this.orgId = this.user1.orgId;
        }
        if (this.user.userName) {
            this.userName = this.user.userName;
        }
        if (this.user.orgName) {
            this.orgName = this.user.orgName;
        }
    }

    // 
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        this.queryCeCardByStatusInfo();
    }


    // 
    queryCeCardByStatusInfo() {

        let param = {
            creditCardNo: this.inValue.creditCardNo
        }
        this.httpService.queryCeCardByStatusInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bean.timeLimit = data.timeLimit;
                this.bean.prodNo = data.prodNo;
                this.bean.prodName = data.prodName;
                this.bean.businessType = data.businessType;
                this.statusAdjustType = data.statusAdjustTypeList,//
                    this.func = data.func;//
                let a = [];
                let funOption = [];
                if (this.statusAdjustType.length > 0) {
                    this.statusAdjustType.forEach(element => {
                        a.push(element[0])
                    });
                }
                for (let i = 0; i < this.statusAdjustTypeList.length; i++) {
                    let bool = true;
                    for (let j = 0; j < a.length; j++) {
                        if (a[j] == this.statusAdjustTypeList[i].value) {
                            bool = false
                            this.statusAdjustTypeAdd.push(this.statusAdjustTypeList[i])
                            break
                        }
                    }
                }
                this.getStatusAdjustTypelIST();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

    // 
    getStatusAdjustTypelIST() {
        // 
        for (let i in this.inValue) {
            this.bean[i] = this.inValue[i];
        }
        this.changeTheme(this.bean.statusAdjustType);
        if (this.bean.func && this.bean.func.length > 0) {
            let ss = this.bean.func.substring(0, this.bean.func.length - 1).split("|")
            for (let i = 0; i < this.funcTypeList.length; i++) {
                let bool = true;
                for (let j = 0; j < ss.length; j++) {
                    if (ss[j] == this.funcTypeList[i].value) {
                        this.funcList.push(ss[j])
                        bool = false
                        break
                    }
                }
            }
        }
    }

    // 
    doSubmitPre(type) {
        // 
        if (this.bean.creditCardNo == '' || this.bean.creditCardNo == undefined || this.bean.creditCardNo == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        if (this.bean.statusAdjustType == '' || this.bean.statusAdjustType == undefined || this.bean.statusAdjustType == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        if (this.bean.statusAdjustType != '2' && this.bean.statusAdjustType != '3') {
            if (this.funcList.length == 0) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                return
            }
        }
        if (this.bean.statusChangeCause == '' || this.bean.statusChangeCause == undefined || this.bean.statusChangeCause == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        if (this.bean.statusChangeSecondCause == '' || this.bean.statusChangeSecondCause == undefined || this.bean.statusChangeSecondCause == null) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        if (type == 'save') {
            // 
            this.bean['adjustType'] = '04'; // 
            if (this.funcList) {
                let ss = ''
                this.funcList.forEach(element => {
                    ss += element + '|'
                });
                this.bean.func = ss;
            }
            this.httpService.updateCeCardAdjustApp(this.bean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
                    this.doUp();
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            }, error => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            })
        } else if (type == 'commit') {
            debugger
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            if (this.inValue.appStatus == '000') {// 
                // 
                this.waitSelUserList = this.commfunc.query('XYK_ZTTZ', {});
                if (!this.waitSelUserList.erro) {
                    this.outVal = this.waitSelUserList.waitSelUserList;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a = commonHeader.extParam.specifyNextOpers;
                    if (this.waitSelUserList.display && (a == undefined || a == '' || a == null)) {
                        this.aaaa = true;
                    } else {
                        this.doSubmit();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
                }
            } else if (this.inValue.appStatus == '991' || this.inValue.appStatus == '994') {// 
                // processIdtaskId
                if (this.inValue.standby2 != null && this.inValue.standby2 != '' && this.inValue.standby2 != undefined) {
                    let queryParam = {
                        processId: this.inValue.standby2,
                    }
                    let arr = [];
                    this.customeHttpService.bpmQueryUnDoTaskList(queryParam).subscribe(data => {
                        if (data.returnCode.code == "success") {
                            arr = data.pageData.result == null ? [] : data.pageData.result;
                            if (arr.length > 0) {
                                this.taskId = arr[0].taskId;

                                this.confirmationService.confirm({
                                    message: '?',
                                    header: '',
                                    accept: () => {
                                        this.updateCeCardStatusAdjustApply();
                                    },
                                    reject: () => {
                                    }
                                });
                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                            }
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: "" });
                    })
                }
            }

        }
    }

    // 
    doSubmit() {
        this.bean.operationType = '2'
        if (this.funcList) {
            let ss = ''
            this.funcList.forEach(element => {
                ss += element + '|'
            });
            this.bean.func = ss;
        }

        this.httpService.updateCeCardStatusAdjustApply(this.bean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                this.doUp();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }

    // 
    doUp() {
        setTimeout(() => {
            this.outValue.emit(false);
        }, 800)
    }

    outDisplayCall(event) {
        this.aaaa = false;
    }

    // 
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.doSubmit();
    }
    // 
    changeTheme(param) {
        this.funcList = [];
        this.funcTypeAdd = [];
        if (param == '2' || param == '3') {
            this.isShow = false;
        } else {
            this.isShow = true;
        }
        let mm = ''
        if (this.statusAdjustType.length > 0) {
            this.statusAdjustType.forEach(element => {
                if (param == element[0]) {
                    mm = element;
                }
            });
        }
        if (mm.indexOf(',') == -1) {
            this.funcTypeAdd = [];
        } else {
            let ss = mm.split(',')[1].split('|');
            for (let i = 0; i < this.funcTypeList.length; i++) {
                let bool = true;
                for (let j = 0; j < ss.length; j++) {
                    if (ss[j] == this.funcTypeList[i].value) {
                        bool = false
                        this.funcTypeAdd.push(this.funcTypeList[i])
                        break
                    }
                }
            }
        }
        let arr = []
        if (this.funcTypeAdd.length >= 1) {
            this.funcTypeAdd.forEach(element => {
                if (element.value != '') {
                    arr.push(element)
                }
            });
            this.funcTypeAdd = [];
            this.funcTypeAdd = arr;
        }
    }


    // 
    updateCeCardStatusAdjustApply() {
        this.bean['adjustType'] = '04'; // 
        if (this.funcList) {
            let ss = ''
            this.funcList.forEach(element => {
                ss += element + '|'
            });
            this.bean.func = ss;
        }
        let Subparam = _.clone(this.bean)
        this.httpService.updateCeCardAdjustApp(Subparam).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bussinessMap['dataMap'] = _.clone(this.bean)
                this.msgs1 = [];
                this.agree()
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }


    // 
    agree() {

        // 
        this.subApproveMapBean.result = '0';
        this.bussinessMap.approve = '0';

        let param = {
            userIdMap: { userId: this.userId },
            proxessDesc: '',
            taskId: this.taskId,
            bussinessMap: this.bussinessMap,
            approveMap: this.subApproveMapBean,
        }
        this.customeHttpService.bpmFindNextNodeInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

                if (data.result.nextOutData && data.result.nextOutData.length >= 1) {
                    let waitSelectUserList = data.result.nextOutData;
                    this.extableData = [];
                    this.extableData = waitSelectUserList;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a = commonHeader.extParam.specifyNextOpers;
                    if (a == undefined || a == '' || a == null) {
                        this.ex = [];
                        // this.juege = false;
                        if (this.extableData && this.extableData.length >= 1) {
                            if (this.extableData.length == 1 &&
                                ((this.extableData[0].selectUserList && this.extableData[0].selectUserList.length == 0) ||
                                    (this.extableData[0].selectUserList == undefined))) {
                                this.examineDisplay = false;
                                // this.juege = true;
                                this.disposeSchedule();
                            } else {
                                if (this.extableData.length == 1) {
                                    this.checkValueNode = this.extableData[0].outFlowId;
                                    if (this.extableData[0].nodeType == 'sign') {

                                    } else {
                                        this.checkValuePeopleSign = [];
                                    }
                                }
                                this.examineDisplay = true;
                            }
                        }

                    } else {
                        // this.juege = true;
                        this.disposeSchedule();
                    }
                } else {
                    this.disposeSchedule();
                }
            }
            else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        }
        )
    }

    // next
    disposeSchedule() {
        this.bussinessMap.specifyNextNode = this.checkValueNode;
        if (this.checkValuePeople) {
            let sIndex = '';
            this.extableData.forEach((element, index) => {
                if (this.checkValueNode == element.outFlowId) {
                    sIndex = index + '';
                }
            });
            if (sIndex.length > 0) {
                let num = this.checkValuePeople.length - sIndex.length;
                this.bussinessMap.specifyNextOpers = this.checkValuePeople.substring(0, num) + ',';
            } else {
                this.bussinessMap.specifyNextOpers = this.checkValuePeople + ',';
            }
        }
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = this.taskId;
        this.customeHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.doUp();
            } else {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs1 = [];
                this.msgs1 = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, error => {
            this.ex = [];
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs1 = [];
            this.msgs1 = [{ severity: 'error', summary: '', detail: '' }];
        })
    }

    // 
    chooseNode(param, rowIndexNode) {

        if (param.outFlowId != this.checkValueNode) {
            this.checkValuePeople = undefined;
        }

        if (param.nodeType == 'sign') {
            this.mm = [];
            if (param.selectUserList && param.selectUserList.length > 0) {
                for (let i = 0; i < param.selectUserList.length; i++) {
                    this.mm.push(param.selectUserList[i].userId)
                }
            }
            this.checkValuePeopleSign = _.clone(this.mm);
        } else {
            this.checkValuePeopleSign = [];
        }
        this.chosseNodeIndex = rowIndexNode;

    }


    choosePeople(col, ri) {
        // 
        this.checkValuePeopleSign = [];
        // 
        this.checkValueNode = col.outFlowId;
    }

    // 
    closeBack() {
        this.examineDisplay = false;
        this.extableData = [];
        this.checkValueNode = undefined;
        this.checkValuePeople = undefined;
        this.checkValuePeopleSign = [];
    }

    // 
    doSubmitSure() {
        if (this.checkValueNode == '' || this.checkValueNode == undefined) {
            this.msgs1 = [];
            this.msgs1.push({ severity: "error", summary: "", detail: "" })
            return
        }

        let ss = '';
        // 
        this.extableData.forEach((element, index) => {
            if (this.checkValueNode == element.outFlowId) {
                ss = element.nodeType
            }
        });

        if (this.checkValuePeople == '' || this.checkValuePeople == undefined) {
            // 
            if (ss == 'sign' || ss == 'auto' || ss == 'end') {

            } else {
                this.msgs1 = [];
                this.msgs1.push({ severity: "error", summary: "", detail: "" })
                return
            }
        }

        // this.juege = true;
        this.examineDisplay = false;
        this.disposeSchedule()

    }
}