import { Routes, RouterModule } from "@angular/router";
import { ModuleWithProviders } from "@angular/core";
import { CardStatusAdjustmentQueryComponent } from "app/pages/tzb/element-adjustment/card-status/card-status-adjustment-query/card-status-adjustment-query.component";
import { CardStatusExamComponent } from "app/pages/tzb/element-adjustment/card-status/card-status-exam/card-status-exam.component";
import { CardStatusAdjustmentComponent } from "app/pages/tzb/element-adjustment/card-status/card-status-adjustment-add/card-status-adjustment-add.component";

export const routes: Routes = [
    {
        path: '',
        children: [
            {path: 'card-card-status-adjustment-query',component: CardStatusAdjustmentQueryComponent},
            {path: 'cardStatusAdjustment',component: CardStatusAdjustmentComponent},
        ]
    },
    {
        path: 'card-status-exam',component: CardStatusExamComponent
    }

]

export const CardStatusRouting: ModuleWithProviders = RouterModule.forChild(routes);