/**
 * @author fanhualing 2018-9-21 
 */
import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { ConfirmationService, Message } from "primeng/primeng";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CardStatusAdjustmentQueryBean } from "app/pages/tzb/element-adjustment/card-status/card-status-adjustment-query/bean/card-status-adjustment-query.bean";
import { Router } from "@angular/router";
import * as _ from 'lodash';
import { AppproResultBean, SubApproveMapBean, BussinessMap } from 'app/pages/tzb/exam-appr-comp/custom/custom-group/bean/AppproResult.bean';
import { CustomeHttpService } from "app/@uisftech/view/http/uisftech-view.http.service";
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';

@Component({
    selector: 'cardStatusAdjustmentQuery',
    templateUrl: './card-status-adjustment-query.component.html',
    styleUrls: ['./card-status-adjustment-query.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CardStatusAdjustmentQueryComponent implements OnInit {

    bean: CardStatusAdjustmentQueryBean = new CardStatusAdjustmentQueryBean();
    statusAdjustType: any = [];
    statusCardAdjustment: any = [];
    unionpayDisposeResult: any = [];
    statusChangeCause: any = [];
    unionpayDisposeResultQuery: any = [];
    msgs: Message[];
    code: any;
    tablelist: any;
    first: number = 0;
    totalNum: number = 0;
    title: string;//
    prodName: string;
    Display: boolean = false;//
    selectProductDisplay: boolean = false;//
    detailInvalue: any;
    upInValue: any;
    isCatelog: boolean = true;

    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;
    info: any;

    // 
    waitSelUserList: any;
    outVal: any;
    aaaa: boolean = false;
    dealPerShow: boolean = false;

    deleteFlag: any = [];
    deleteParam: any;
    isCommitDis: boolean = true;

    // ------------fanhl------------------
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    bussinessMap: BussinessMap = new BussinessMap();
    examineDisplay: any;
    extableData: any;
    ex: any = [];
    checkValueNode: string;  //
    checkValuePeople: string;
    checkValuePeopleSign: any = []; //
    mm: any = [];
    chosseNodeIndex: string;
    msgs1: Message[];
    taskId: string;

    userInValue: any;
    personPage: boolean = false;
    orgTreeDisplay: boolean = false;
    agentName: string;
    ownerOrgInValue: any;
    handleOrgName: string;
    ch: any = CALENDAR_CN;
    startDate: Date = null;
    endDate: Date = null;

    constructor(
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService,
        public httpService: CusOperationHttpService,
        public router: Router,
        public customeHttpService: CustomeHttpService,
    ) {
        //
        this.codeValues();
        this.statusAdjustType = this.code['StatusAdjustType'];
        this.statusCardAdjustment = this.code['statusCardAdjustment'];
        this.unionpayDisposeResultQuery = this.code['UnionpayDisposeResultQuery'];

        this.unionpayDisposeResult = this.code['UnionpayDisposeResult'];
        this.statusChangeCause = this.code['StatusChangeCause'];

        this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));

        // 
        if (this.user1.userId) {
            this.userId = this.user1.userId;
        }
        if (this.user1.orgId) {
            this.orgId = this.user1.orgId;
        }
        if (this.user.userName) {
            this.userName = this.user.userName;
        }
        if (this.user.orgName) {
            this.orgName = this.user.orgName;
        }
    }

    // 
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.querySecle();
    }
    //
    reset() {
        this.bean = new CardStatusAdjustmentQueryBean();
        this.prodName = undefined;
        this.agentName = undefined;
        this.handleOrgName = undefined;
        this.startDate = null;
        this.endDate = null;
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
    }

    // 
    querySecle() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.doQuery();
    }

    // 
    doQuery() {
        // 
        // 
        if (this.bean.custNo == '') {
            this.bean.custNo = undefined;
        }
        // 
        if (this.bean.custName == '') {
            this.bean.custName = undefined;
        }
        var reg = /^[0-9]*$/;
        var reg1 = /^[A-Za-z0-9]+$/;
        if (this.bean.custNo != undefined) {
            if (this.bean.custNo != '') {
                if (!reg.test(this.bean.custNo)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    return
                }
                if (this.bean.custNo.length != 10) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '10' });
                    return
                }
            }
        }
        if (this.bean.custName != undefined) {
            if (this.bean.custName != '') {
                if (reg.test(this.bean.custName)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    return
                }
            }
        }
        // 
        if (this.bean.appSeqNo == '') {
            this.bean.appSeqNo = undefined;
        }
        if (this.bean.appSeqNo != undefined) {
            if (this.bean.appSeqNo != '') {
                if (!reg.test(this.bean.appSeqNo)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    return
                }
            }
        }
        // 
        if (this.bean.prodName == '') {
            this.bean.prodName = undefined;
        }
        // 
        if (this.bean.creditCardNo == '') {
            this.bean.creditCardNo = undefined;
        }
        if (this.bean.creditCardNo != undefined) {
            if (this.bean.creditCardNo != '') {
                if (!reg.test(this.bean.creditCardNo)) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    return
                }
            }
        }
        //
        if (this.bean.statusChangeCause == '') {
            this.bean.statusChangeCause = undefined;
        }
        //
        if (this.bean.statusChangeSecondCause == '') {
            this.bean.statusChangeSecondCause = undefined;
        }
        //
        if (this.bean.statusAdjustType == '') {
            this.bean.statusAdjustType = undefined;
        }
        // ,,
        if (this.startDate != undefined && this.startDate != null) {
            this.bean.startDate = this.commfunc.transDateN(this.startDate);
        } else {
            this.bean.startDate = undefined;
        }
        if (this.endDate != undefined && this.endDate != null) {
            this.bean.endDate = this.commfunc.transDateN(this.endDate);
        } else {
            this.bean.endDate = undefined;
        }
        let ob = this.httpService.queryCeCardStatusAdjustApply(this.bean);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tablelist = data.infoList;
                this.totalNum = data.totalNum;
                this.isCommitDis = true;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                this.isCommitDis = true;
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
            this.isCommitDis = true;
        })
    }

    //
    addDisplay: boolean = false;//
    add() {
        this.router.navigate(["/pages/tzb/element-adjustment/card-status/cardStatusAdjustment"]);
    }

    //
    detailsDisplay: boolean = false;//
    toDetails(col) {
        this.title = "";
        this.Display = true;
        this.addDisplay = false;
        this.detailsDisplay = true;
        this.updataDisplay = false;
        this.detailInvalue = col;
    }

    detailCall() {
        this.detailsDisplay = false;
        this.Display = false;
    }

    // 
    updataDisplay: boolean = false;//
    toEdit(col) {
        this.title = "";
        this.Display = true;
        this.addDisplay = false;
        this.detailsDisplay = false;
        this.updataDisplay = true;
        this.upInValue = col;
    }

    // 
    updateCall() {
        this.updataDisplay = false;
        this.Display = false;
        this.querySecle();
    }

    // 
    delete(item) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                let arr = [];
                arr.push({ appSeqNo: item.appSeqNo, creditCardNo: item.creditCardNo, adjustType: item.adjustType });
                let param = {
                    infoList: arr
                };
                this.httpService.deleteCeCardAppInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.querySecle();
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        });
    }

    // 
    choosePro() {
        this.selectProductDisplay = true;
    }

    // 
    closeSetting1() {
        this.selectProductDisplay = false;
    }

    // 
    productCall(event) {
        if (event && event.length > 0) {
            this.prodName = event[0].productName;
            this.bean.prodNo = event[0].productId;
        }
        this.selectProductDisplay = false;
    }
    //
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.doQuery();
    }


    // 
    toCommit(col) {
        this.info = {};
        this.info = _.clone(col);
        if (col.appStatus == '000') {// 
            this.confirmationService.confirm({
                message: '?',
                header: '',
                accept: () => {
                    let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    if (s.extParam.specifyNextOpers) {
                        delete s.extParam.specifyNextOpers
                        let commonHeader = s;
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                    }
                    // 
                    this.waitSelUserList = this.commfunc.query('XYK_ZTTZ', {});
                    if (!this.waitSelUserList.erro) {
                        this.outVal = this.waitSelUserList.waitSelUserList;
                        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                        let a = commonHeader.extParam.specifyNextOpers;
                        if (this.waitSelUserList.display && (a == undefined || a == '' || a == null)) {
                            this.aaaa = true;
                        } else {
                            this.doSubmit();
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: this.waitSelUserList.erro });
                    }
                }
            });
        } else if (col.appStatus == '991' || col.appStatus == '994') {// 
            debugger
            // processIdtaskId
            if (col.standby2 != null && col.standby2 != '' && col.standby2 != undefined) {
                let queryParam = {
                    processId: col.standby2,
                }
                let arr = [];
                this.customeHttpService.bpmQueryUnDoTaskList(queryParam).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        arr = data.pageData.result == null ? [] : data.pageData.result;
                        if (arr.length > 0) {
                            this.taskId = arr[0].taskId;

                            this.confirmationService.confirm({
                                message: '?',
                                header: '',
                                accept: () => {

                                    this.subApproveMapBean.result = '0';
                                    this.bussinessMap.approve = '0';
                                    // if (this.info.updateType == '1') {
                                    //     debugger
                                    //     // this.bussinessMap['dataMap'] = this.info;
                                    // } else if (this.info.updateType == '2') {
                                    //     // this.bussinessMap['dataMap'] = this.info;
                                    // }
                                    this.bussinessMap['dataMap'] = this.info;
                                    setTimeout(() => {
                                        this.msgs1 = [];
                                        this.agree()
                                    }, 500)
                                },
                                reject: () => {
                                }
                            });
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: '' });
                        }
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        }

    }

    outDisplayCall(event) {
        this.aaaa = false;
    }

    // 
    outValueCall(event) {
        this.outVal = event;
        let ss = '';
        this.outVal.forEach(item => {
            ss += item.tellerId + ','
        });
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = ss;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        this.doSubmit();
    }

    doSubmit() {
        this.isCommitDis = false;
        this.info['operationType'] = '2'; // 
        this.httpService.updateCeCardStatusAdjustApply(this.info).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                // this.doUp();
                if (this.info.appStatus == '000') {// 
                    this.doUp();
                } else if (this.info.appStatus == '991' || this.info.appStatus == '994') {// 
                    this.disposeSchedule();
                }
            } else {
                this.isCommitDis = true;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.isCommitDis = true;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
            let commonHeader = s;
            this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
    }

    doUp() {
        setTimeout(() => {
            // this.reset();
            this.querySecle();
        }, 800)
    }
    //
    toDelete(item) {
        this.deleteFlag = [];
        this.deleteParam = JSON.parse(JSON.stringify(item));
        this.confirmationService.confirm({
            message: '?',
            header: '',
            accept: () => {
                //
                this.deleteFlag.push({
                    appSeqNo: this.deleteParam['appSeqNo'],
                    creditCardNo: this.deleteParam['creditCardNo'],
                    adjustType: '04',//
                });
                this.commitDelete();
            }
        });
    }
    //
    commitDelete() {
        this.httpService.deleteCeCardAppInfo({ infoList: this.deleteFlag }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: "!" });
                this.querySecle();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        });
    }

    // 
    agree() {

        let param = {
            userIdMap: { userId: this.userId },
            proxessDesc: '',
            taskId: this.taskId,
            bussinessMap: this.bussinessMap,
            approveMap: this.subApproveMapBean,
        }
        this.customeHttpService.bpmFindNextNodeInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

                if (data.result.nextOutData && data.result.nextOutData.length >= 1) {
                    let waitSelectUserList = data.result.nextOutData;
                    this.extableData = [];
                    this.extableData = waitSelectUserList;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    let a = commonHeader.extParam.specifyNextOpers;
                    if (a == undefined || a == '' || a == null) {
                        this.ex = [];
                        // this.juege = false;
                        if (this.extableData && this.extableData.length >= 1) {
                            if (this.extableData.length == 1 &&
                                ((this.extableData[0].selectUserList && this.extableData[0].selectUserList.length == 0) ||
                                    (this.extableData[0].selectUserList == undefined))) {
                                this.examineDisplay = false;
                                // this.juege = true;
                                this.disposeSchedule();
                            } else {
                                if (this.extableData.length == 1) {
                                    this.checkValueNode = this.extableData[0].outFlowId;
                                    if (this.extableData[0].nodeType == 'sign') {

                                    } else {
                                        this.checkValuePeopleSign = [];
                                    }
                                }
                                this.examineDisplay = true;
                            }
                        }

                    } else {
                        // this.juege = true;
                        this.disposeSchedule();
                    }
                } else {
                    this.disposeSchedule();
                }
            }
            else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        }
        )
    }

    // next
    disposeSchedule() {
        this.bussinessMap.specifyNextNode = this.checkValueNode;
        if (this.checkValuePeople) {
            let sIndex = '';
            this.extableData.forEach((element, index) => {
                if (this.checkValueNode == element.outFlowId) {
                    sIndex = index + '';
                }
            });
            if (sIndex.length > 0) {
                let num = this.checkValuePeople.length - sIndex.length;
                this.bussinessMap.specifyNextOpers = this.checkValuePeople.substring(0, num) + ',';
            } else {
                this.bussinessMap.specifyNextOpers = this.checkValuePeople + ',';
            }
        }
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = this.taskId;
        this.customeHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.doUp();
            } else {
                this.isCommitDis = true;
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                this.msgs1 = [];
                this.msgs1 = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, error => {
            this.isCommitDis = true;
            this.ex = [];
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            this.msgs1 = [];
            this.msgs1 = [{ severity: 'error', summary: '', detail: '' }];
        })
    }

    // 
    chooseNode(param, rowIndexNode) {

        if (param.outFlowId != this.checkValueNode) {
            this.checkValuePeople = undefined;
        }

        if (param.nodeType == 'sign') {
            this.mm = [];
            if (param.selectUserList && param.selectUserList.length > 0) {
                for (let i = 0; i < param.selectUserList.length; i++) {
                    this.mm.push(param.selectUserList[i].userId)
                }
            }
            this.checkValuePeopleSign = _.clone(this.mm);
        } else {
            this.checkValuePeopleSign = [];
        }
        this.chosseNodeIndex = rowIndexNode;

    }


    choosePeople(col, ri) {
        // 
        this.checkValuePeopleSign = [];
        // 
        this.checkValueNode = col.outFlowId;
    }

    // 
    closeBack() {
        this.examineDisplay = false;
        this.extableData = [];
        this.checkValueNode = undefined;
        this.checkValuePeople = undefined;
        this.checkValuePeopleSign = [];
    }

    // 
    doSubmitSure() {
        if (this.checkValueNode == '' || this.checkValueNode == undefined) {
            this.msgs1 = [];
            this.msgs1.push({ severity: "error", summary: "", detail: "" })
            return
        }

        let ss = '';
        // 
        this.extableData.forEach((element, index) => {
            if (this.checkValueNode == element.outFlowId) {
                ss = element.nodeType
            }
        });

        if (this.checkValuePeople == '' || this.checkValuePeople == undefined) {
            // 
            if (ss == 'sign' || ss == 'auto' || ss == 'end') {

            } else {
                this.msgs1 = [];
                this.msgs1.push({ severity: "error", summary: "", detail: "" })
                return
            }
        }

        // this.juege = true;
        this.examineDisplay = false;
        this.disposeSchedule()
        // this.doSubmit();

    }

    //
    chooseWorker() {
        this.userInValue = '0000';
        this.personPage = !this.personPage;
    }
    //
    onHideClose() {
        this.personPage = false;
        this.orgTreeDisplay = false;
    }
    //
    perInfor(item) {
        this.personPage = false;
        if (item.length > 0) {
            this.bean.agent = item[0].tellerId;
            this.agentName = item[0].tellerName;
        }
    }
    //
    openOrg(param) {
        this.orgTreeDisplay = !this.orgTreeDisplay;
        this.ownerOrgInValue = 'adjustOrg'
    }
    //
    treeCall(param) {
        this.orgTreeDisplay = false;
        this.handleOrgName = param.orgName;
        this.bean.handleOrg = param.orgId;
    }

    // 
    showTime(param1, param2) {
        if (param1 == 'plan') {
            if (this.startDate && this.endDate) {
                if (this.startDate > this.endDate) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.startDate = null;
                        } else {
                            this.endDate = null;
                        }
                    })
                }
            }
        }
    }

    // 
    handInputDate(param1, param2) {
        this.showTime(param1, param2);
    }

    compileStr(cust){
        return this.commfunc.compileStr(cust)
    }

    // 
    doExport() {
        let InParam = {};
        let InParamIn = {};
        InParam = _.clone(this.bean);
        if (InParam['commonHeader']) {
            delete InParam['commonHeader']
        }
        // ,,
        if (this.startDate != undefined && this.startDate != null) {
            InParam['startDate'] = this.commfunc.transDateN(this.startDate);
        } else {
            InParam['startDate'] = undefined;
        }
        if (this.endDate != undefined && this.endDate != null) {
            InParam['endDate'] = this.commfunc.transDateN(this.endDate);
        } else {
            InParam['endDate'] = undefined;
        }
        InParamIn['condition'] = InParam;
        InParamIn['adjustType'] = '04';
        this.httpService.cecardAdjustExport(InParamIn).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let aa = data.fileName;
                window.open(TZB_CUS_DOWNLOAD + "?fileName=" + encodeURI(aa) + "&fileUrl=" + data.fileUrl)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }  
}